$(document).ready(function(){
    var code = '';

    function createCaptcha() {
        var codeLength = 4;
        code = '';
        $('#captcha-code').text('');

        var selectChar = new Array(2, 3, 4, 5, 6, 7, 8, 9, 'A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'V', 'W', 'X', 'Y', 'Z');
        for (var i = 0; i < codeLength; i++) {
            var charIndex = Math.floor(Math.random() * 32);
            code += selectChar[charIndex];
        }

        $('#captcha-code').text(code);
        $('#captcha-input').val('');
    }

    createCaptcha();

    function validate() {
        var scode = $('#code-input').val();
        var ccode = $('#captcha-input').val().toUpperCase();
        if (scode == "") {
            alert("请输入防伪码");
            return false;
        }
        var antiFakeCodeReg = /^\d{20}$|^\d{21}$|^\d{22}$/;
        if (!antiFakeCodeReg.test(scode)) {
            alert("请输入正确的防伪码");
            return false;
        }
        if (ccode.length <= 0) {
            alert("请输入验证码");
            return false;
        } else if (ccode != code) {
            alert("验证码输入错误");
            createCaptcha();
            return false;
        } else {
            var par = "http://www.app315.net/AntiFakeQueryResult.aspx?Code=" + scode;
            window.open(par, "", "toolbar=no,status=yes,resizable=yes,width=1124px,height=800px");
            createCaptcha();
            return true;
        }
    }

    $('.query-btn').click(validate);

})
